.archipelago-subtheme-nysa #drupal-off-canvas-wrapper tr {
  border-bottom: none;
}
.archipelago-subtheme-nysa #drupal-off-canvas-wrapper th:first-child {
  --off-canvas-table-cell-padding: 2px;
