}

.archipelago-subtheme-nysa #drupal-off-canvas-wrapper table tr th,
.archipelago-subtheme-nysa #drupal-off-canvas-wrapper table tr th a,
.archipelago-subtheme-nysa #drupal-off-canvas-wrapper table tr th a:hover,
